@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


body {
  /* FONT */
  font-family: 'Roboto Mono', monospace;
  letter-spacing: 2px;

  /* COLORS */
  background-color: #353535;
  color: #FFFFFF;

}


.page {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.title {
  font-size: 40px;
  color: #F0A81C;
  text-align: center;
  margin-bottom: 9%;
}

.box {
  /* POSITION */
  width: 60%;
}

.widget {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.text-box {
  border: 2px solid #756C6C80;
  line-height: 30px;
  border-radius: 10px;
  height: 150px;
  padding: 10px;
  color: #888888;
  text-overflow: clip;
  overflow: hidden;
}

.text-box:focus {
  outline-width: 0;
  text-overflow: clip;
  overflow: hidden;
}

.text-placeholder {
  position: absolute;
  width: 58%;
  height: 150px;

  text-overflow: clip;
  overflow: hidden;
}

.text-placeholder::after {
  content: attr(data-placeholder);
}

textarea {
  background: transparent;
  border: none;
  resize: none;
}

.text-input {
  color: transparent;
  position: absolute;
  width: 58%;
  height: 150px;
  outline-width: 0;
  line-height: 30px;
  font-family: 'Roboto Mono', monospace;
  letter-spacing: 2px;
  font-size: 16px;
  padding: 0px;
  text-overflow: clip;
  overflow: hidden;
}

.active {
  color: #FFFFFF;
}

.error {
  color: red;
}
.widget-box {
  border: 2px solid #756C6C80;
  border-radius: 10px;
  min-height: 54px;
  min-width: 73px;
  padding: 0 10px;
  text-align: center;
  margin: 40px 40px;
}
